body {
    /*background-color: lightslategrey;*/
    color: lightblue;
}

ul li {
    list-style-type: inherit;
}

a {
    color: white;
    text-decoration: none;
}

a.active {
    color: darkgreen;
    text-decoration: underline;
}

a:hover {
    color: darkgreen;
}

.app-wrapper {
    display: grid;

    margin: 0 auto;

    grid-template-areas: "l h" "n c" "footer footer";

    grid-template-rows: 13vh minmax(82vh, max-content) 5vh;
    grid-template-columns: 10vw 70vw;
    width: 80vw;
}

.app-wrapper-content {
    grid-area: c;

    background-color: rgb(188,178,59);
    margin: 0.5vw;
    padding: 0.5vw;
}

